import Vue from 'vue'
import Vuex from 'vuex'
import router from '../router'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    staticurl: '',
    drawer: null,
    user: JSON.parse(localStorage.getItem('fitme-admin-user')||"{}"),
    token: localStorage.getItem('fitme-admin-token') || null,
    message: {
      status: true,
      text: '',
      toggle: false
    }
  },
  getters: {
    drawer: (state) => state.drawer,
    user: (state) => state.user,
    token: (state) => state.token,
    message: (state) => !!state.message,
    isLoggedIn: (state) => !!state.token,
    url: (state) => state.staticurl,
    user_id: state => state.user._id,
  },
  mutations: {
    toggleNav: (state) => state.drawer = !state.drawer,
    closeSnackbar: (state) => state.message = {
      status: true,
      text: '',
      toggle: false
    },
    openSnackbar: (state, data) => state.message = data,
    LoginUser: (state, data) => {
      state.user = data.user
      state.token = data.token
      localStorage.setItem('fitme-admin-token', data.token)
      localStorage.setItem('fitme-admin-user', JSON.stringify(data.user))
    },
    logout: state => {
      state.user = null;
      state.token = null;
      localStorage.removeItem('fitme-admin-token')
      localStorage.removeItem('fitme-admin-user')
      router.push('/login')
    }
  },
  actions: {
  },
  modules: {
  }
})
