<template>
  <v-card outlined tile>
    <v-card-title class="text-h5"> План питания </v-card-title>
    <div class="d-flex flex-column">
      <v-card flat v-for="time in itemDetailtimes" :key="time.title">
        <v-card-text>
          <strong class="text-h6 ml-5">{{ time.title }}</strong>
          <v-slide-group center-active show-arrows>
            <v-slide-item
              v-for="(food, i) in plan?.[time.el]"
              :key="i" v-slot="{ toggle }">
              <v-card class="ma-1" height="140" width="160" @click="toggle">
                <v-img v-if="food?.image"
                  :src="`${url}/${food?.image}`"
                  height="160px" class="white--text">
                  <v-card-title class="align-self-start justify-self-center">
                  </v-card-title>
                  <v-card-title>10 kkal</v-card-title>
                  <v-card-subtitle>{{ food.title }}</v-card-subtitle>
                  <v-card-actions>
                    <v-btn icon @click="$emit('deleteProd', { id: plan._id, food: food._id, time: time.el })">
                      <v-icon color="white" size="30"> mdi-trash-can </v-icon>
                    </v-btn>
                  </v-card-actions>
                </v-img>
              </v-card>
            </v-slide-item>
          </v-slide-group>
        </v-card-text>
      </v-card>
    </div>
    <v-card-actions>
      <v-btn color="blue darken-1" text @click="$emit('closewind')"> Отмена </v-btn>
      <v-spacer></v-spacer>
      <v-menu offset-y>
        <template v-slot:activator="{ on, attrs }">
          <v-btn icon v-bind="attrs" v-on="on">
            <v-avatar color="brown" size="30">
              <img :src="`${url}/${plan?.author?.image}`" v-if="plan?.author?.image" />
            </v-avatar>
          </v-btn>
        </template>
        <v-list>
          <v-list-item v-for="(a, ind) of plan?.author" :key="ind">
            <v-list-item-title>{{ ind }}: {{ a }}</v-list-item-title>
          </v-list-item>
        </v-list>
      </v-menu>
    </v-card-actions>
  </v-card>
</template>

<script>
import { mapGetters } from "vuex";

export default {
  name: "ViewPlan",
  props: ["plan"],
  data: () => ({
    itemDetailtimes: [
      { title: "Завтрак", el: "breakfast" },
      { title: "Обед", el: "dinner" },
      { title: "Ужин", el: "supper" },
    ],
    model: "",
  }),
  computed: mapGetters(["url"]),
};
</script>

