<template>
  <v-container fluid>
    <v-card tile class="mx-auto mt-10 pb-5 glass" max-width="430">
      <v-toolbar flat dark>
        <v-toolbar-title>Tizimga Kirish</v-toolbar-title>
      </v-toolbar>
      <v-card-text>
      </v-card-text>
      <form class="mx-5" @submit.prevent="login">
        <v-text-field
          class="rounded-0"
          outlined
          required
          label="Email"
          v-model="formdata.email"
        ></v-text-field>
        <v-text-field
          class="rounded-0"
          outlined
          required
          v-model="formdata.password"
          label="Parol"
          :append-icon="show ? 'mdi-eye' : 'mdi-eye-off'"
          :type="show ? 'text' : 'password'"
          @click:append="show = !show"
        ></v-text-field>
        <v-btn tile elevation="0" class="py-6" block type="submit" dark>
          Kirish
        </v-btn>
      </form>
    </v-card>
    <v-snackbar v-model="message.toggle" timeout="2000">
      {{ message.message }}
      <template v-slot:action="{ attrs }">
        <v-btn :color="message.status?'green':'red'" 
          text v-bind="attrs" @click="message.toggle=false">
          Close
        </v-btn>
      </template>
    </v-snackbar>
  </v-container>
</template>

<script>
import { mapMutations } from 'vuex'
export default {
  name: "Login",
  data: () => ({
    show: null,
    formdata: {
      email: '',
      password: '',
    },
    message: {
      status: true,
      message: '',
      toggle: false
    },
  }),
  methods: {
    ...mapMutations(['LoginUser']),
    async login(){
      try{
        const { data } = await this.$http.post('/auth/adminlogin', this.formdata);
        if(data.status){
          this.showMessage({...data, toggle: true});
          this.LoginUser(data)
          this.$router.push('/')
        }
      }catch(err){
        this.showMessage({...err.response.data, toggle: true});
      }
    },
    showMessage(data){
      this.message = data;
    },
  }
};
</script>

