<template>
  <v-data-table
    :headers="headers"
    :items="items"
    :search="search"
    class="elevation-0"
    :single-expand="false"
    :expanded.sync="expanded"
    item-key="_id"
    show-expand
  >
    <!-- <template v-slot:top>
      <div class="d-flex py-3 px-3">
        <v-row class="d-flex justify-space-between">
          <v-col cols="12" sm="4">
            <v-text-field
              v-model="search"
              append-icon="mdi-magnify"
              label="Search"
              outlined
              hide-details
              dense
            ></v-text-field>
          </v-col>
        </v-row>
      </div>
    </template> -->
    <template v-slot:item.actions="{ item }">
      <v-menu offset-y>
        <template v-slot:activator="{ on, attrs }">
          <v-btn v-bind="attrs" v-on="on" icon>
            <v-icon>mdi-dots-horizontal</v-icon>
          </v-btn>
        </template>
        <v-list dense>
          <v-list-item link @click="editItem(item)">
            <v-list-item-title>Edit</v-list-item-title>
          </v-list-item>
          <v-list-item link @click="deleteItem(item)">
            <v-list-item-title>Delete</v-list-item-title>
          </v-list-item>
        </v-list>
      </v-menu>
    </template>

    <template v-slot:item.author="{ item }">
      <span>{{ item.author?.name }}</span>
    </template>
    <!-- <template v-slot:item.parent="{ item }">
      <span>{{ item.parent?'None':'None' }}</span>
    </template> -->
    <template v-slot:expanded-item="{ headers, item }">
      <td :colspan="headers.length">
        <div style="display: flex; gap: 10px; padding: 10px; flex-wrap: wrap">
          <div v-for="ct in item.child" :key="ct._id">
            <v-menu offset-y dense>
              <template v-slot:activator="{ on, attrs }">
                <v-chip v-bind="attrs" v-on="on" label dark color="blue">
                  {{ ct.title }}
                </v-chip>
              </template>
              <v-list>
                <v-list-item link @click="editItem(ct)">
                  <v-list-item-title>Edit</v-list-item-title>
                </v-list-item>
                <v-list-item link @click="deleteItem(ct)">
                  <v-list-item-title>Delete</v-list-item-title>
                </v-list-item>
              </v-list>
            </v-menu>
          </div>
        </div>
      </td>
    </template>
  </v-data-table>
</template>

<script>
export default {
  props: ["items"],
  data: () => ({
    headers: [
      {
        text: "Title",
        align: "start",
        value: "title",
      },
      { text: "Author", value: "author" },
    //   { text: "Parent", value: "parent" },
      { text: "Date", value: "createdAt" },
      { text: "Actions", value: "actions", sortable: false },
    ],
    expanded: [],
    search: "",
  }),
  methods: {
    editItem(item) {
      this.$emit("edit", item);
    },
    deleteItem(item) {
      this.$emit("delet", item);
    },
  },
};
</script>

