<template>
  <v-container fluid>
    <v-expansion-panels multiple>
      <v-expansion-panel
        v-for="({ title, icon, field }, i) in categories"
        :key="i"
      >
        <v-expansion-panel-header>
          <div>
            <v-chip color="deep-purple" label text-color="white">
              <v-icon left> {{ icon }} </v-icon>
              {{ title }}
            </v-chip>
          </div>
        </v-expansion-panel-header>
        <v-expansion-panel-content>
          <CategoryTable
            :items="filterData(field)"
            @edit="editItem"
            @delet="deleteItem"
          />
        </v-expansion-panel-content>
      </v-expansion-panel>
    </v-expansion-panels>
    <v-dialog v-model="dialog" max-width="400px">
      <template v-slot:activator="{ on, attrs }">
        <v-btn color="primary" dark class="mb-2" v-bind="attrs" v-on="on">
          Add
        </v-btn>
      </template>
      <v-card>
        <v-form ref="form" v-model="valid" lazy-validation>
          <v-card-title>
            <span class="text-h5">{{ formTitle }}</span>
          </v-card-title>

          <v-card-text>
            <v-container>
              <v-row>
                <v-col cols="12">
                  <v-text-field
                    v-model="editedItem.title"
                    label="Название"
                    :rules="nameRules"
                    outlined
                    hide-details
                    dense
                  >
                  </v-text-field>
                </v-col>
                <v-col cols="12">
                  <v-select
                    :items="categories"
                    label="Тема"
                    :rules="nameRules"
                    outlined
                    hide-details
                    dense
                    v-model="editedItem.theme"
                    item-text="title"
                    item-value="field"
                    clearable
                  ></v-select>
                </v-col>
                <v-col cols="12">
                  <v-select
                    :items="desserts"
                    label="Родительская категория"
                    outlined
                    hide-details
                    dense
                    v-model="editedItem.parent"
                    item-text="title"
                    item-value="_id"
                    clearable
                  ></v-select>
                </v-col>
              </v-row>
            </v-container>
          </v-card-text>

          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="blue darken-1" text @click="close"> Cancel </v-btn>
            <v-btn
              color="blue darken-1"
              text
              :disabled="!valid"
              @click="validate"
            >
              Save
            </v-btn>
          </v-card-actions>
        </v-form>
      </v-card>
    </v-dialog>
    <v-dialog v-model="dialogDelete" max-width="400px">
      <v-card>
        <v-card-title class="text-h5"
          >Are you sure you want to delete this item?
        </v-card-title>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="blue darken-1" text @click="closeDelete">Cancel</v-btn>
          <v-btn color="blue darken-1" text @click="deleteItemConfirm"
            >OK</v-btn
          >
          <v-spacer></v-spacer>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-snackbar v-model="snackbar" :timeout="2000">
      {{ snacktext }}
      <template v-slot:action="{ attrs }">
        <v-btn
          :color="snackstatus ? 'green' : 'red'"
          text
          v-bind="attrs"
          @click="snackbar = false">
          Закрывать
        </v-btn>
      </template>
    </v-snackbar>
  </v-container>
</template>
  
<script>
import CategoryTable from "@/components/CategoryTable.vue";
import { mapGetters } from "vuex";

export default {
  name: "Category",
  data: () => ({
    categories: [
      {
        field: "product",
        title: "База продуктов",
        color: "pink accent-3",
        icon: "mdi-baguette",
      },
      {
        field: "mealplan",
        title: "Планы Питания",
        color: "",
        icon: "mdi-food",
      },
      {
        field: "exercise",
        title: "База упражнений",
        color: "",
        icon: "mdi-dumbbell",
      },
      {
        field: "workout",
        title: "Програмы тренировок",
        color: "",
        icon: "mdi-weight-lifter",
      },
    ],

    valid: false,
    snackbar: false,
    snacktext: "",
    snackstatus: true,

    desserts: [],
    dialog: false,
    dialogDelete: false,
    editedIndex: -1,
    editedId: null,
    editedItem: {
      name: "",
      theme: "",
      parent: null,
      author: "",
    },
    defaultItem: {
      name: "",
      theme: "",
      parent: null,
      author: "",
    },
    nameRules: [(v) => !!v || "Укажите поля"],
  }),
  computed: {
    ...mapGetters(["user_id"]),
    formTitle() {
      return this.editedIndex === -1 ? "New Item" : "Edit Item";
    },
  },

  watch: {
    dialog(val) {
      val || this.close();
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
  },

  created() {
    this.initialize();
  },

  methods: {
    async initialize() {
      const { data } = await this.$http.get("/category/parent");
      this.desserts = data.result;
    },

    validate() {
      if (this.$refs.form.validate()) {
        this.save();
      }
    },
    openSnackbar(text, status) {
      this.snackbar = true;
      this.snacktext = text;
      this.snackstatus = status;
    },

    filterData(filt) {
      return this.desserts.filter((item) => item.theme === filt);
    },

    editItem(item) {
      this.editedIndex = this.desserts.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
      this.editedId = item._id;
    },

    deleteItem(item) {
      this.editedIndex = this.desserts.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialogDelete = true;
      this.editedId = item._id;
    },

    async deleteItemConfirm() {
      const { data } = await this.$http.delete(
        `/category/delete/${this.editedId}`
      );
      if (data.status) {
        if (data.result.parent) {
          const ind = this.desserts
            .find((i) => i._id == data.result.parent)
            .child.indexOf(data.result);
          this.desserts
            .find((i) => i._id == data.result.parent)
            .child.splice(ind, 1);
        } else {
          this.desserts.splice(this.editedIndex, 1);
        }
        this.openSnackbar(data.message, data.status);
        this.closeDelete();
      }
    },

    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
        this.editedId = null;
      });
    },

    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
        this.editedId = null;
      });
    },

    async save() {
      if (this.editedIndex > -1 || this.editedId) {
        delete this.editedItem.parent;
        const { data } = await this.$http.put(
          `/category/edit/${this.editedId}`,
          this.editedItem
        );
        if (data.result?.parent) {
          Object.assign(
            this.desserts
              .find((item) => item._id == data.result.parent)
              .child.find((item) => item._id == this.editedId),
            data.result
          );
        } else {
          Object.assign(this.desserts[this.editedIndex], data.result);
        }
        this.openSnackbar(data.message, data.status);
      } else {
        this.editedItem.author = this.user_id;
        const { data } = await this.$http.post(
          "/category/add",
          this.editedItem
        );
        if (data.result.parent) {
          this.desserts
            .find((item) => item._id === data.result.parent)
            .child.push(data.result);
        } else {
          this.desserts.push(data.result);
        }
        this.openSnackbar(data.message, data.status);
      }
      this.close();
    },
  },
  components: {
    CategoryTable,
  },
};
</script>
  
