import Vue from 'vue'
import VueRouter from 'vue-router'
import store from '@/store/index'

import Admins from '@/views/Admins.vue'
import Dashboard from '@/views/Dashboard.vue'
import Exercises from '@/views/Exercises.vue'
import Feedback from '@/views/Feedback.vue'
import Finance from '@/views/Finance.vue'
import Login from '@/views/Login.vue'
import MealPlans from '@/views/MealPlans.vue'
import Products from '@/views/Products.vue'
import Trainers from '@/views/Trainers.vue'
import Users from '@/views/Users.vue'
import Workout from '@/views/Workout.vue'
import Category from '@/views/Category.vue'

Vue.use(VueRouter)

const beforeEnter = (_, __, next) => {
  if(!store.getters.isLoggedIn){
    next('/login')
  }else{
    next()
  }
}

const beforeEnter1 = (_, __, next) => {
  if(store.getters.isLoggedIn){
    next('/')
  }else{
    next()
  }
}

const routes = [
  {
    path: '/',
    name: 'dashboard',
    component: Dashboard,
    beforeEnter,
  },
  {
    path: '/users',
    name: 'users',
    component: Users,
    beforeEnter,
  },
  {
    path: '/trainers',
    name: 'trainers',
    component: Trainers,
    beforeEnter,
  },
  {
    path: '/admins',
    name: 'admins',
    component: Admins,
    beforeEnter,
  },
  {
    path: '/products',
    name: 'products',
    component: Products,
    beforeEnter,
  },
  {
    path: '/finance',
    name: 'finance',
    component: Finance,
    beforeEnter,
  },
  {
    path: '/mealplans',
    name: 'mealplans',
    component: MealPlans,
    beforeEnter,
  },
  {
    path: '/exercises',
    name: 'exercises',
    component: Exercises,
    beforeEnter,
  },
  {
    path: '/workout',
    name: 'workout',
    component: Workout,
    beforeEnter,
  },
  {
    path: '/feedback',
    name: 'feedback',
    component: Feedback,
    beforeEnter,
  },
  {
    path: '/categories',
    name: 'categories',
    component: Category,
    beforeEnter,
  },
  {
    path: '/login',
    name: 'login',
    component: Login,
    beforeEnter: beforeEnter1,
  },
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
