<template>
  <v-container fluid>
    <v-data-table
      :headers="headers"
      :items="desserts"
      :search="search"
      class="elevation-2"
      item-key="_id"
      :expanded="expanded"
      show-expand
      single-expand
    >
      <template v-slot:top>
        <div class="d-flex py-3 px-3">
          <v-row class="d-flex justify-space-between">
            <v-col cols="6" sm="6" md="6">
              <v-text-field
                v-model="search"
                append-icon="mdi-magnify"
                label="Поиск"
                solo
                hide-details
                dense
              ></v-text-field>
            </v-col>
            <v-col cols="4" sm="6" md="6" class="d-flex justify-end">
              <v-spacer></v-spacer>
              <v-dialog v-model="dialog" max-width="400px">
                <template v-slot:activator="{ on, attrs }">
                  <v-btn dark class="mb-2" v-bind="attrs" v-on="on">
                    Добавыть
                  </v-btn>
                </template>
                <v-card>
                  <v-form ref="form" v-model="valid" lazy-validation>
                    <v-card-title>
                      <span class="text-h5">{{ formTitle }}</span>
                    </v-card-title>

                    <v-card-text>
                      <v-container>
                        <v-row>
                          <v-col cols="12">
                            <v-text-field
                              v-model="editedItem.title"
                              label="Название"
                              outlined
                              hide-details
                              dense
                              :rules="nameRules"
                            >
                            </v-text-field>
                          </v-col>
                          <v-col cols="6">
                            <v-text-field
                              v-model="editedItem.price"
                              label="Цена"
                              outlined
                              hide-details
                              min="0"
                              dense
                              type="number"
                            >
                            </v-text-field>
                          </v-col>
                          <v-col cols="6">
                            <v-text-field
                              v-model="editedItem.weeks"
                              label="Неделя"
                              outlined
                              hide-details
                              min="0"
                              dense
                              type="number"
                              :rules="nameRules"
                            >
                            </v-text-field>
                          </v-col>
                          <v-col cols="6">
                            <v-text-field
                              v-model="editedItem.workouts"
                              label="Тренировки"
                              outlined
                              hide-details
                              min="0"
                              dense
                              type="number"
                              :rules="nameRules"
                            >
                            </v-text-field>
                          </v-col>
                          <v-col cols="6">
                            <v-btn block :disabled="editedItem.workouts == 0"
                              >Тренировки</v-btn
                            >
                          </v-col>
                          <v-col cols="6">
                            <v-select
                              :items="tags"
                              label="Уровень тренировки"
                              outlined
                              hide-details
                              dense
                              v-model="editedItem.level"
                              :rules="[(v) => !!v || 'Укажите уровень']"
                            >
                            </v-select>
                          </v-col>
                          <!-- <v-col cols="12">
                            <v-select
                              :items="exercises"
                              label="Тренировки"
                              outlined
                              hide-details
                              multiple
                              dense
                              v-model="editedItem.exercises"
                              item-value="_id"
                              item-text="title"
                            >
                            </v-select>
                          </v-col> -->
                          <v-col cols="6">
                            <v-select
                              :items="['Женской', 'Мужской']"
                              label="Пол"
                              outlined
                              hide-details
                              dense
                              v-model="editedItem.male"
                              :rules="[(v) => !!v || 'Укажите пола']"
                            >
                            </v-select>
                          </v-col>
                          <v-col cols="12">
                            <div class="d-flex align-center">
                              <v-avatar>
                                <img :src="urlImage" />
                              </v-avatar>
                              <v-file-input
                                label="Добавить фото"
                                outlined
                                dense
                                v-model="file"
                                hide-details
                              >
                              </v-file-input>
                            </div>
                          </v-col>
                          <v-col cols="12">
                            <v-text-field
                              v-model="editedItem.metadescription"
                              label="Короткое описание"
                              outlined
                              hide-details
                              dense
                              :rules="nameRules"
                            >
                            </v-text-field>
                          </v-col>
                          <v-col cols="12">
                            <v-textarea
                              outlined
                              name="input-7-4"
                              :rules="nameRules"
                              label="Описание"
                              v-model="editedItem.description"
                              hide-details
                            >
                            </v-textarea>
                          </v-col>
                        </v-row>
                      </v-container>
                    </v-card-text>

                    <v-card-actions>
                      <v-spacer></v-spacer>
                      <v-btn color="blue darken-1" text @click="close">
                        Отмена
                      </v-btn>
                      <v-btn
                        color="blue darken-1"
                        :disabled="!valid"
                        text
                        @click="validate"
                      >
                        Сохранять
                      </v-btn>
                    </v-card-actions>
                  </v-form>
                </v-card>
              </v-dialog>
              <v-dialog v-model="dialogDelete" max-width="400px">
                <v-card outlined tile>
                  <v-card-title class="text-h5">
                    Вы уверены, что хотите удалить этот элемент?
                  </v-card-title>
                  <v-card-text
                    >Внимание. После того, как информация будет удалена, она не
                    может быть восстановлена!</v-card-text
                  >
                  <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="blue darken-1" text @click="closeDelete"
                      >Cancel
                    </v-btn>
                    <v-btn color="blue darken-1" text @click="deleteItemConfirm"
                      >OK
                    </v-btn>
                  </v-card-actions>
                </v-card>
              </v-dialog>
            </v-col>
          </v-row>
        </div>
      </template>
      <template v-slot:item.image="{ item }">
        <v-avatar rounded>
          <img :src="`${url}/${item.image}`" />
        </v-avatar>
      </template>
      <template v-slot:item.author="{ item }">
        <span>{{ item.author?.name }}</span>
      </template>
      <template v-slot:item.date="{ item }">
        <span>{{ date(item._id) }}</span>
      </template>
      <template v-slot:item.public="{ item }">
        <v-chip :color="item.public ? 'green' : 'red'" dark small>
          {{ item.public ? "public" : "private" }}
        </v-chip>
      </template>
      <template v-slot:item.actions="{ item }">
        <v-menu offset-y>
          <template v-slot:activator="{ on, attrs }">
            <v-btn v-bind="attrs" v-on="on" icon>
              <v-icon>mdi-dots-horizontal</v-icon>
            </v-btn>
          </template>
          <v-list dense>
            <v-list-item link @click="viewItem(item._id)">
              <v-list-item-title>View</v-list-item-title>
            </v-list-item>
            <v-list-item link @click="editItem(item)">
              <v-list-item-title>Edit</v-list-item-title>
            </v-list-item>
            <v-list-item link @click="deleteItem(item)">
              <v-list-item-title>Delete</v-list-item-title>
            </v-list-item>
          </v-list>
        </v-menu>
      </template>
      <template v-slot:expanded-item="{ headers, item }">
        <td :colspan="headers.length" class="py-2 px-4">
          <v-card class="elevation-0">
            <v-card-title>Описание</v-card-title>
            <v-card-text>
              {{ item.description }}
            </v-card-text>
          </v-card>
        </td>
      </template>
    </v-data-table>
    <v-snackbar v-model="snackbar" :timeout="2000">
      {{ snacktext }}
      <template v-slot:action="{ attrs }">
        <v-btn
          :color="snackstatus ? 'green' : 'red'"
          text
          v-bind="attrs"
          @click="snackbar = false"
        >
          Закрывать
        </v-btn>
      </template>
    </v-snackbar>
    <v-dialog v-model="dialogView" max-width="600px">
      <ViewWorkout :item="vItem" @closewind="closeView" />
    </v-dialog>
  </v-container>
</template>

<script>
import { mapGetters } from "vuex";
import ViewWorkout from "@/components/ViewWorkout.vue";

export default {
  data: () => ({
    expanded: [],
    tags: ["Начальный", "Опытный", "Продвинутый"],
    valid: false,
    dialog: false,
    dialogDelete: false,
    dialogView: false,
    snackbar: false,
    snacktext: "",
    snackstatus: true,
    headers: [
      { text: "Картина", value: "image", sortable: false, align: "start" },
      { text: "Название", value: "title" },
      { text: "Цена", value: "price" },
      { text: "Пол", value: "male", sortable: false },
      //   { text: "Описание", value: "description", sortable: false },
      { text: "Короткое описание", value: "metadescription", sortable: false },
      { text: "Уровень тренировки", value: "level" },
      { text: "Автор", value: "author" },
      { text: "Дата", value: "date" },
      { text: "Действия", value: "actions", sortable: false },
    ],
    desserts: [],
    search: "",
    editedIndex: -1,
    editedId: null,
    file: null,
    productList: [],
    product: {
      title: "",
      description: "",
      approach: "",
      filename: "",
    },
    productItem: {
      title: "",
      description: "",
      approach: "",
      filename: "",
    },
    editedItem: {
      author: "",
      title: "",
      description: "",
      metadescription: "",
      level: "",
      price: 0,
      weeks: 0,
      workouts: 0,
      male: "",
    },
    defaultItem: {
      author: "",
      title: "",
      description: "",
      metadescription: "",
      level: "",
      price: 0,
      weeks: 0,
      workouts: 0,
      male: "",
    },
    nameRules: [(v) => !!v || "Укажите поля"],
    vItem: {},

    productTime: "",
    planProducts: [],
    model: "",
    exercises: [],
  }),

  components: {
    ViewWorkout,
  },

  computed: {
    ...mapGetters(["url", "user_id"]),
    formTitle() {
      return this.editedIndex === -1 ? "Добавить" : "Редактировать";
    },
    urlImage() {
      if (this.file) {
        return URL.createObjectURL(this.file);
      } else {
        return "/icons/nophoto.jpg";
      }
    },
  },

  watch: {
    dialog(val) {
      val || this.close();
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
    dialogView(val) {
      val || this.closeView();
    },
  },

  created() {
    this.initialize();
    this.getAllExers();
  },

  methods: {
    validate() {
      if (this.$refs.form.validate()) {
        this.save();
      }
    },
    openSnackbar(text, status) {
      this.snackbar = true;
      this.snacktext = text;
      this.snackstatus = status;
    },

    async initialize() {
      const { data } = await this.$http.get("/training/all");
      if (data.status) {
        this.desserts = data.result;
      }
      const products = await this.$http.get("/training/all");
      if (products.data.status) {
        this.productList = products.data.result;
      }
    },

    async viewItem(id) {
      this.dialogView = true;
      const { data } = await this.$http.get(`/training/find/?_id=${id}`);
      if (data.status) {
        this.vItem = data.result;
      }
    },

    date(d) {
      return new Date(parseInt(d.toString().substring(0, 8), 16) * 1000)
        .toString()
        .slice(0, 24);
    },

    editItem(item) {
      this.editedId = item._id;
      this.editedIndex = this.desserts.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },

    deleteItem(item) {
      this.editedId = item._id;
      this.editedIndex = this.desserts.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialogDelete = true;
    },

    async deleteItemConfirm() {
      const { data } = await this.$http.delete(
        `/training/delete/${this.editedId}`
      );
      if (data.status) {
        this.desserts.splice(this.editedIndex, 1);
      }
      this.openSnackbar(data.message, data.status);
      this.closeDelete();
    },

    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
        this.editedId = null;
        this.file = null;
      });
    },

    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
        this.editedId = null;
        this.file = null;
      });
    },
    closeView() {
      this.dialogView = false;
      this.$nextTick(() => {
        this.vItem = null;
        this.planProducts = [];
        this.productTime = "";
      });
    },

    async getAllExers() {
      const { data } = await this.$http.get("/exercise/all");
      if (data.status) {
        this.exercises = data.result;
      }
    },

    async save() {
      const formdata = new FormData();
      Object.keys(this.editedItem).map((key) => {
        formdata.append(key, this.editedItem[key]);
      });
      if (this.file) {
        formdata.set("filename", Date.now() + "_" + this.file.name);
        formdata.append("file", this.file);
      }
      if (this.editedIndex > -1) {
        formdata.delete("_id");
        formdata.delete("exercises");
        formdata.delete("author");
        formdata.delete("createdAt");
        formdata.delete("updatedAt");
        formdata.delete("__v");
        const { data } = await this.$http.put(
          `/training/edit/${this.editedId}`,
          formdata
        );
        if (data.status) {
          Object.assign(this.desserts[this.editedIndex], data.result);
        }
        console.log(data);
        this.openSnackbar(data.message, data.status);
      } else {
        formdata.set("author", this.user_id);
        const { data } = await this.$http.post("/training/add/", formdata);
        if (data.status) {
          this.desserts.push(data.result);
        }
        this.openSnackbar(data.message, data.status);
      }
      this.close();
    },

    async addProduct(items) {
      const { data } = await this.$http.put(
        `/training/addexer/${this.vItem._id}`,
        {
          items,
        }
      );
      if (data.status) {
        this.vItem[this.productTime] = data.result[this.productTime];
        this.close1();
      }
    },
  },
  mounted() {
    window.alert("ведутся технические работы");
  },
};
</script>

<style>
</style>