<template>
  <v-container fluid>
    <v-data-table
      :headers="headers"
      :items="desserts"
      :search="search"
      class="elevation-2"
      item-key="_id"
      :expanded="expanded" show-expand single-expand
    >
      <template v-slot:top>
        <div class="d-flex py-3 px-3">
          <v-row class="d-flex justify-space-between">
            <v-col cols="6" sm="6" md="6">
              <v-text-field
                v-model="search"
                append-icon="mdi-magnify"
                label="Поиск"
                solo
                hide-details
                dense
              ></v-text-field>
            </v-col>
            <v-col cols="4" sm="6" md="6" class="d-flex justify-end">
              <v-spacer></v-spacer>
              <v-dialog v-model="dialog" max-width="400px">
                <template v-slot:activator="{ on, attrs }">
                  <v-btn dark class="mb-2" v-bind="attrs" v-on="on">
                    Добавить
                  </v-btn>
                </template>
                <v-card>
                  <v-form ref="form" v-model="valid" lazy-validation>
                    <v-card-title>
                      <span class="text-h5">{{ formTitle }}</span>
                    </v-card-title>

                    <v-card-text>
                      <v-container>
                        <v-row>
                          <v-col cols="12">
                            <v-text-field
                              v-model="editedItem.title"
                              label="Название"
                              outlined
                              hide-details
                              dense
                              :rules="nameRules"
                            >
                            </v-text-field>
                          </v-col>
                          <v-col cols="12">
                            <v-text-field
                              v-model="editedItem.approach"
                              label="Подходы"
                              min="0"
                              outlined
                              hide-details
                              dense
                              type="number"
                              :rules="nameRules"
                            >
                            </v-text-field>
                          </v-col>
                          <v-col cols="12">
                            <v-select
                              v-model="editedItem.category"
                              label="Категория"
                              :items="tags"
                              item-text="title"
                              item-value="title"
                              outlined
                              hide-details
                              dense
                              :rules="nameRules"
                            >
                            </v-select>
                          </v-col>
                          <v-col cols="12" v-if="editedItem.category">
                            <v-select
                              v-model="editedItem.childcategory"
                              label="Подкатегория"
                              :items="getChildCateg(editedItem.category)"
                              item-text="title"
                              item-value="title"
                              outlined
                              hide-details
                              dense
                              :rules="nameRules"
                            >
                            </v-select>
                          </v-col>
                          <v-col cols="12">
                            <v-text-field
                              v-model="editedItem.description"
                              label="Короткое описание"
                              outlined
                              hide-details
                              dense
                              :rules="nameRules"
                            >
                            </v-text-field>
                          </v-col>
                          <v-col cols="12">
                            <div class="d-flex align-center">
                              <video
                                :src="urlImage"
                                autoplay
                                loop
                                muted
                                style="object-fit: cover"
                                width="100"
                              ></video>
                              <v-file-input
                                accept="video/mp4,video/x-m4v,video/*"
                                :label="!editedId
                                    ? 'Добавить видео'
                                    : 'Поменить видео'"
                                outlined
                                dense
                                v-model="file"
                                hide-details
                              ></v-file-input>
                            </div>
                          </v-col>
                        </v-row>
                      </v-container>
                    </v-card-text>

                    <v-card-actions>
                      <v-spacer></v-spacer>
                      <v-btn color="blue darken-1" text @click="close">
                        Отмена
                      </v-btn>
                      <v-btn
                        color="blue darken-1"
                        :disabled="!valid"
                        text
                        @click="validate">
                        Сохранить
                      </v-btn>
                    </v-card-actions>
                  </v-form>
                </v-card>
              </v-dialog>
              <v-dialog v-model="dialogDelete" max-width="400px">
                <v-card outlined tile>
                  <v-card-title class="text-h5">
                    Вы уверены, что хотите удалить этот элемент?
                  </v-card-title>
                  <v-card-text
                    >Внимание. После того, как информация будет удалена, она не
                    может быть восстановлена!</v-card-text
                  >
                  <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="blue darken-1" text @click="closeDelete"
                      >Cancel
                    </v-btn>
                    <v-btn color="blue darken-1" text @click="deleteItemConfirm"
                      >OK
                    </v-btn>
                  </v-card-actions>
                </v-card>
              </v-dialog>
            </v-col>
          </v-row>
        </div>
      </template>
      <template v-slot:item.date="{ item }">
        <span>{{ date(item.updatedAt) }}</span>
      </template>
      <template v-slot:item.public="{ item }">
        <v-chip :color="item.public ? 'green' : 'red'" dark small>
          {{ item.public ? "public" : "private" }}
        </v-chip>
      </template>
      <template v-slot:item.actions="{ item }">
        <v-menu offset-y>
          <template v-slot:activator="{ on, attrs }">
            <v-btn v-bind="attrs" v-on="on" icon>
              <v-icon>mdi-dots-horizontal</v-icon>
            </v-btn>
          </template>
          <v-list dense>
            <v-list-item link @click="editItem(item)">
              <v-list-item-title>Edit</v-list-item-title>
            </v-list-item>
            <v-list-item link @click="deleteItem(item)">
              <v-list-item-title>Delete</v-list-item-title>
            </v-list-item>
          </v-list>
        </v-menu>
      </template>
      <template v-slot:expanded-item="{ headers, item }">
        <td :colspan="headers.length" class="py-2 px-4">
          <v-card class="elevation-0">
            <v-card-title>Описание</v-card-title>
            <v-card-text>
              {{item.description}}
            </v-card-text>
          </v-card>
        </td>
      </template>
    </v-data-table>
    <v-snackbar v-model="snackbar" :timeout="2000">
      {{ snacktext }}
      <template v-slot:action="{ attrs }">
        <v-btn
          :color="snackstatus ? 'green' : 'red'"
          text
          v-bind="attrs"
          @click="snackbar = false"
        >
          Закрывать
        </v-btn>
      </template>
    </v-snackbar>
  </v-container>
</template>

<script>
import { mapGetters } from 'vuex';
export default {
  data: () => ({
    expanded: [],
    tags: [],
    valid: false,
    dialog: false,
    dialogDelete: false,
    snackbar: false,
    snacktext: "",
    snackstatus: true,
    headers: [
      { text: "Название", value: "title", sortable: false, align: "start" },
      { text: "Подходы", value: "approach", sortable: false },
      { text: "Категория", value: "category", sortable: false },
      { text: "Подкатегория", value: "childcategory", sortable: false },
      { text: "Дата", value: "date" },
      { text: "Действия", value: "actions", sortable: false },
    ],
    desserts: [],
    search: "",
    editedIndex: -1,
    editedId: null,
    file: null,
    editedItem: {
      title: "",
      description: "",
      approach: "",
      category: "",
      childcategory: "",
      filename: "",
    },
    defaultItem: {
      title: "",
      description: "",
      approach: "",
      category: "",
      childcategory: "",
      filename: "",
    },
    nameRules: [(v) => !!v || "Укажите поля"],
  }),

  computed: {
    ...mapGetters(['url']),
    formTitle() {
      return this.editedIndex === -1 ? "Добавить" : "Редактировать";
    },
    urlImage() {
      if (this.file) {
        return URL.createObjectURL(this.file);
      } else if (this.editedId && !this.file) {
        return `${this.url}/${this.editedItem.video}`;
      } else {
        return "/icons/nophoto.jpg";
      }
    },
  },

  watch: {
    dialog(val) {
      val || this.close();
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
  },

  created() {
    this.initialize();
    this.getCategory();
  },

  methods: {
    validate() {
      if (this.$refs.form.validate()) {
        this.save();
      }
    },
    openSnackbar(text, status) {
      this.snackbar = true;
      this.snacktext = text;
      this.snackstatus = status;
    },
    getChildCateg(ct) {
      return this.tags.find(c => c.title == ct).child;
    },

    date(d) {
      return new Date(parseInt(d.toString().substring(0, 8), 16) * 1000)
        .toString()
        .slice(0, 24);
    },

    async initialize() {
      const { data } = await this.$http.get("/exercise/all");
      if (data.status) {
        this.desserts = data.result;
      }
    },
    async getCategory() {
      const { data } = await this.$http.get("/category/find?theme=exercise");
      if (data.status) {
        this.tags = data.result;
      }
    },

    editItem(item) {
      this.editedId = item._id;
      this.editedIndex = this.desserts.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },

    deleteItem(item) {
      this.editedId = item._id;
      this.editedIndex = this.desserts.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialogDelete = true;
    },

    async deleteItemConfirm() {
      const { data } = await this.$http.delete(`/exercise/delete/${this.editedId}`);
      if (data.status) {
        this.desserts.splice(this.editedIndex, 1);
      }
      this.openSnackbar(data.message, data.status);
      this.closeDelete();
    },

    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
        this.editedId = null;
        this.file = null;
      });
    },

    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
        this.editedId = null;
        this.file = null;
      });
    },

    async save() {
      const formdata = new FormData();
      Object.keys(this.editedItem).map((key) => {
        formdata.append(key, this.editedItem[key]);
      });
      if (this.file) {
        formdata.set("filename", Date.now() + "_" + this.file.name);
        formdata.append("file", this.file);
      }
      if (this.editedIndex > -1) {
        const { data } = await this.$http.put(
          `/exercise/edit/${this.editedId}`,
          formdata
        );
        if (data.status) {
          Object.assign(this.desserts[this.editedIndex], data.result);
        }
        this.openSnackbar(data.message, data.status);
      } else {
        const { data } = await this.$http.post("/exercise/add/", formdata);
        if (data.status) {
          this.desserts.push(data.result);
        }
        this.openSnackbar(data.message, data.status);
      }
      this.close();
    },
  },
};
</script>

