<template>
  <v-container fluid>
    <v-data-table
      :headers="headers"
      :items="desserts"
      :search="search"
      class="elevation-2"
      item-key="_id"
      :expanded="expanded" show-expand single-expand
    >
      <template v-slot:top>
        <div class="d-flex py-3 px-3">
          <v-row class="d-flex justify-space-between">
            <v-col cols="6" sm="6" md="6">
              <v-text-field
                v-model="search"
                append-icon="mdi-magnify"
                label="Поиск"
                solo
                hide-details
                dense
              ></v-text-field>
            </v-col>
            <v-col cols="4" sm="6" md="6" class="d-flex justify-end">
              <v-spacer></v-spacer>
              <v-dialog v-model="dialog" max-width="400px">
                <template v-slot:activator="{ on, attrs }">
                  <v-btn dark class="mb-2" v-bind="attrs" v-on="on">
                    Добавить
                  </v-btn>
                </template>
                <v-card>
                  <v-form ref="form" v-model="valid" lazy-validation>
                    <v-card-title>
                      <span class="text-h5">{{ formTitle }}</span>
                    </v-card-title>

                    <v-card-text>
                      <v-container>
                        <v-row>
                          <v-col cols="12">
                            <v-text-field
                              v-model="editedItem.title"
                              label="Название"
                              outlined
                              hide-details
                              dense
                              :rules="nameRules"
                            >
                            </v-text-field>
                          </v-col>
                          <v-col cols="12">
                            <v-text-field
                              v-model="editedItem.price"
                              label="Цена"
                              min="0"
                              outlined
                              hide-details
                              dense
                              type="number"
                              :rules="nameRules"
                            >
                            </v-text-field>
                          </v-col>
                          <v-col cols="12">
                              <v-select :items="['Жиросжигание', 'Массанабор']" label="Тип"
                                  outlined hide-details dense
                                  v-model="editedItem.type"
                                  :rules="[v => !!v || 'Укажите типу']">
                              </v-select>
                          </v-col>
                          <!-- <v-col cols="12">
                              <v-select :items="tags" label="Категория"
                                  outlined hide-details dense
                                  v-model="editedItem.category"
                                  :rules="[v => !!v || 'Укажите категорию']">
                              </v-select>
                          </v-col> -->
                          <v-col cols="12">
                              <v-select :items="productList" label="Завтрак"
                                  outlined hide-details dense multiple 
                                  item-text="title" item-value="_id"
                                  v-model="editedItem.breakfast">
                              </v-select>
                          </v-col>
                          <v-col cols="12">
                              <v-select :items="productList" label="Обед"
                                  outlined hide-details dense multiple
                                  item-text="title" item-value="_id"
                                  v-model="editedItem.dinner">
                              </v-select>
                          </v-col>
                          <v-col cols="12">
                              <v-select :items="productList" label="Ужин"
                                  outlined hide-details dense multiple
                                  item-text="title" item-value="_id"
                                  v-model="editedItem.supper">
                              </v-select>
                          </v-col>
                          <v-col cols="12">
                            <v-select
                              v-model="editedItem.category"
                              label="Категория"
                              :items="tags"
                              item-text="title"
                              item-value="title"
                              outlined
                              hide-details
                              dense
                              :rules="nameRules"
                            >
                            </v-select>
                          </v-col>
                          <v-col cols="12" v-if="editedItem.category">
                            <v-select
                              v-model="editedItem.childcategory"
                              label="Подкатегория"
                              :items="getChildCateg(editedItem.category)"
                              item-text="title"
                              item-value="title"
                              outlined
                              hide-details
                              dense
                              :rules="nameRules"
                            >
                            </v-select>
                          </v-col>
                          <v-col cols="12" sm="6">
                            <v-text-field
                              v-model="editedItem.carbohydrates"
                              label="Срж. углеводов (в гр.)"
                              min="0"
                              outlined
                              hide-details
                              dense
                              type="number"
                              :rules="nameRules"
                            >
                            </v-text-field>
                          </v-col>
                          <v-col cols="12" sm="6">
                            <v-text-field
                              v-model="editedItem.fat"
                              label="Срж. жиров (в гр.)"
                              min="0"
                              outlined
                              hide-details
                              dense
                              type="number"
                              :rules="nameRules"
                            >
                            </v-text-field>
                          </v-col>
                          <v-col cols="12" sm="6">
                            <v-text-field
                              v-model="editedItem.proteins"
                              label="Срж. белков (в гр.)"
                              min="0"
                              outlined
                              hide-details
                              dense
                              type="number"
                              :rules="nameRules"
                            >
                            </v-text-field>
                          </v-col>
                          <v-col cols="12" sm="6">
                            <v-text-field
                              v-model="editedItem.calories"
                              label="Срж. калориев (в гр.)"
                              min="0"
                              outlined
                              hide-details
                              dense
                              type="number"
                              :rules="nameRules"
                            >
                            </v-text-field>
                          </v-col>

                          <v-col cols="12">
                            <v-textarea
                              outlined
                              name="input-7-4"
                              :rules="nameRules"
                              label="Описание"
                              v-model="editedItem.description"
                              hide-details
                            >
                            </v-textarea>
                          </v-col>
                        </v-row>
                      </v-container>
                    </v-card-text>

                    <v-card-actions>
                      <v-spacer></v-spacer>
                      <v-btn color="blue darken-1" text @click="close">
                        Отмена
                      </v-btn>
                      <v-btn
                        color="blue darken-1"
                        :disabled="!valid"
                        text
                        @click="validate"
                      >
                        Сохранить
                      </v-btn>
                    </v-card-actions>
                  </v-form>
                </v-card>
              </v-dialog>
              <v-dialog v-model="dialogDelete" max-width="400px">
                <v-card outlined tile>
                  <v-card-title class="text-h5">
                    Вы уверены, что хотите удалить этот элемент?
                  </v-card-title>
                  <v-card-text
                    >Внимание. После того, как информация будет удалена, она не
                    может быть восстановлена!</v-card-text
                  >
                  <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="blue darken-1" text @click="closeDelete"
                      >Cancel
                    </v-btn>
                    <v-btn color="blue darken-1" text @click="deleteItemConfirm"
                      >OK
                    </v-btn>
                  </v-card-actions>
                </v-card>
              </v-dialog>
            </v-col>
          </v-row>
        </div>
      </template>
      <template v-slot:item.image="{ item }">
        <v-avatar rounded>
          <img :src="`/files/${item.image}`" />
        </v-avatar>
      </template>
      <template v-slot:item.author="{ item }">
        <span>{{ item.author?.name }}</span>
      </template>
      <template v-slot:item.date="{ item }">
        <span>{{ date(item._id) }}</span>
      </template>
      <template v-slot:item.public="{ item }">
        <v-chip :color="item.public ? 'green' : 'red'" dark small>
          {{ item.public ? "public" : "private" }}
        </v-chip>
      </template>
      <template v-slot:item.actions="{ item }">
        <v-menu offset-y>
          <template v-slot:activator="{ on, attrs }">
            <v-btn v-bind="attrs" v-on="on" icon>
              <v-icon>mdi-dots-horizontal</v-icon>
            </v-btn>
          </template>
          <v-list dense>
            <v-list-item link @click="viewItem(item._id)">
              <v-list-item-title>View</v-list-item-title>
            </v-list-item>
            <v-list-item link @click="editItem(item)">
              <v-list-item-title>Edit</v-list-item-title>
            </v-list-item>
            <v-list-item link @click="deleteItem(item)">
              <v-list-item-title>Delete</v-list-item-title>
            </v-list-item>
          </v-list>
        </v-menu>
      </template>
      <template v-slot:expanded-item="{ headers, item }">
        <td :colspan="headers.length" class="py-2 px-4">
          <v-card class="elevation-0">
            <v-card-title>Описание</v-card-title>
            <v-card-text>
              {{item.description}}
            </v-card-text>
          </v-card>
        </td>
      </template>
    </v-data-table>
    <v-snackbar v-model="snackbar" :timeout="2000">
      {{ snacktext }}
      <template v-slot:action="{ attrs }">
        <v-btn
          :color="snackstatus ? 'green' : 'red'"
          text
          v-bind="attrs"
          @click="snackbar = false"
        >
          Закрывать
        </v-btn>
      </template>
    </v-snackbar>
    <v-dialog v-model="dialogView" max-width="600px">
      <ViewPlan :plan="vItem" @deleteProd="deleteProduct" @closewind="closeView"/>
    </v-dialog>
  </v-container>
</template>

<script>
import ViewPlan from '@/components/ViewPlan.vue';
import { mapGetters } from 'vuex';

export default {
  data: () => ({
    expanded: [],
    tags: [],
    valid: false,
    dialog: false,
    dialogDelete: false,
    dialogView: false,
    snackbar: false,
    snacktext: "",
    snackstatus: true,
    headers: [
      { text: "Название", value: "title", align: "start" },
      { text: "Цена", value: "price" },
      { text: "Автор", value: "author" },
      { text: "Углеводы", value: "carbohydrates" },
      { text: "Жиры", value: "fat" },
      { text: "Белок", value: "proteins" },
      { text: "Калории", value: "calories" },
      { text: "Дата", value: "date" },
      { text: "Действия", value: "actions", sortable: false },
    ],
    desserts: [],
    search: "",
    editedIndex: -1,
    editedId: null,
    file: null,
    productList: [],
    editedItem: {
      author: "",
      title: "",
      price: 0,
      carbohydrates: "",
      fat: "",
      proteins: "",
      calories: "",
      description: "",
      dinner: [],
      breakfast: [],
      category: "",
      childcategory: "",
      supper: [],
      type: ''
    },
    defaultItem: {
      author: "",
      title: "",
      price: 0,
      carbohydrates: "",
      fat: "",
      proteins: "",
      calories: "",
      description: "",
      category: "",
      childcategory: "",
      dinner: [],
      breakfast: [],
      supper: [],
      type: ''
    },
    nameRules: [(v) => !!v || "Укажите поля"],
    vItem: {},

    productTime: "",
    planProducts: [],
  }),
  components: {
    ViewPlan,
  },
  computed: {
    ...mapGetters(['user_id']),
    formTitle() {
      return this.editedIndex === -1 ? "Добавить" : "Редактировать";
    },
    urlImage() {
      if (this.file) {
        return URL.createObjectURL(this.file);
      } else {
        return "/files/nophoto.jpg";
      }
    },
  },

  watch: {
    dialog(val) {
      val || this.close();
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
    dialogView(val) {
      val || this.closeView();
    },
  },

  created() {
    this.initialize();
    this.getCategory();
  },

  methods: {
    validate() {
      if (this.$refs.form.validate()) {
        this.save();
      }
    },
    openSnackbar(text, status) {
      this.snackbar = true;
      this.snacktext = text;
      this.snackstatus = status;
    },

    getChildCateg(ct) {
      return this.tags.find(c => c.title == ct).child;
    },

    async getCategory() {
      const { data } = await this.$http.get("/category/find?theme=mealplan");
      if (data.status) {
        this.tags = data.result;
      }
    },

    async initialize() {
      const { data } = await this.$http.get("/plan/all");
      if (data.status) {
        this.desserts = data.result;
      }
      const products = await this.$http.get("/product/all");
      if (products.data.status) {
        this.productList = products.data.result;
      }
    },

    async viewItem(id) {
      const { data } = await this.$http.get(`/plan/find/?_id=${id}`);
      if (data.status) {
        this.vItem = data.result;
        this.dialogView = true;
      }
    },
    getTimeMealPlan(n) {
      switch (n) {
        case 1:
          return ["Завтрак", "breakfast"];
        case 2:
          return ["Обед", "dinner"];
        case 3:
          return ["Ужин", "supper"];
        default:
          return ["", ""];
      }
    },

    date(d) {
      return new Date(parseInt(d.toString().substring(0, 8), 16) * 1000)
        .toString()
        .slice(0, 24);
    },

    editItem(item) {
      this.editedId = item._id;
      this.editedIndex = this.desserts.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },

    deleteItem(item) {
      this.editedId = item._id;
      this.editedIndex = this.desserts.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialogDelete = true;
    },

    async deleteItemConfirm() {
      const { data } = await this.$http.delete(`/plan/delete/${this.editedId}`);
      if (data.status) {
        this.desserts.splice(this.editedIndex, 1);
      }
      this.openSnackbar(data.message, data.status);
      this.closeDelete();
    },

    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
        this.editedId = null;
        this.file = null;
      });
    },
    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
        this.editedId = null;
        this.file = null;
      });
    },
    closeView() {
      this.dialogView = false;
      this.$nextTick(() => {
        this.vItem = null;
        this.planProducts = [];
        this.productTime = "";
      });
    },

    async deleteProduct({food, id, time}){
      const { data } = await this.$http.put(`/plan/removeprod/${id}/${time}`, { id: food })
      this.vItem[time] = data.result[time];
    },

    async save() {
      if (this.editedIndex > -1) {
        delete this.editedItem.author;
        delete this.editedItem.breakfast;
        delete this.editedItem.dinner;
        delete this.editedItem.supper;
        const { data } = await this.$http.put(`/plan/edit/${this.editedId}`, this.editedItem);
        if (data.status) {
          Object.assign(this.desserts[this.editedIndex], data.result);
        }
        this.openSnackbar(data.message, data.status);
      } else {
        this.editedItem.author = this.user_id;
        const { data } = await this.$http.post("/plan/add/", this.editedItem);
        console.log(data);
        if (data.status) {
          this.desserts.push(data.result);
        }
        this.openSnackbar(data.message, data.status);
      }
      this.close();
    },

    async addProduct(items) {
      const { data } = await this.$http.put(
        `/plan/addprod/${this.vItem._id}/${this.productTime}`,
        { items }
      );
      if (data.status) {
        this.vItem[this.productTime] = data.result[this.productTime];
        this.close1();
      }
    },
  },
};
</script>

