<template>
  <v-container fluid>
    <v-data-table
      :headers="headers"
      :items="desserts"
      :search="search"
      class="elevation-2"
    >
      <template v-slot:top>
        <div class="d-flex py-3 px-3">
          <v-row class="d-flex justify-space-between">
            <v-col cols="6" sm="6" md="6">
              <v-text-field
                v-model="search"
                append-icon="mdi-magnify"
                label="Поиск"
                solo
                hide-details
                dense
              ></v-text-field>
            </v-col>
            <v-col cols="4" sm="6" md="6" class="d-flex justify-end">
              <v-spacer></v-spacer>
              <v-dialog v-model="dialog" max-width="400px">
                <template v-slot:activator="{ on, attrs }">
                  <v-btn dark class="mb-2" v-bind="attrs" v-on="on">
                    Добавить
                  </v-btn>
                </template>
                <v-card>
                  <v-form ref="form" v-model="valid" lazy-validation>
                    <v-card-title>
                      <span class="text-h5">{{ formTitle }}</span>
                    </v-card-title>

                    <v-card-text>
                      <v-container>
                        <v-row>
                          <v-col cols="12">
                            <v-text-field
                              v-model="editedItem.title"
                              label="Название"
                              outlined
                              hide-details
                              dense
                              :rules="nameRules"
                            >
                            </v-text-field>
                          </v-col>
                          <v-col cols="12">
                            <v-text-field
                              v-model="editedItem.portion"
                              label="1 порция гр."
                              min="0"
                              outlined
                              hide-details
                              dense
                              type="number"
                              :rules="nameRules"
                            >
                            </v-text-field>
                          </v-col>
                          <v-col cols="12">
                            <v-select
                              :items="tags"
                              label="Категория"
                              outlined
                              hide-details
                              dense
                              item-text="title"
                              item-value="title"
                              v-model="editedItem.category"
                              :rules="[(v) => !!v || 'Укажите категорию']"
                            >
                            </v-select>
                          </v-col>
                          <v-col cols="12">
                            <v-text-field
                              v-model="editedItem.cookingtime"
                              label="Готовка ( в мин. )"
                              min="0"
                              outlined
                              hide-details
                              dense
                              type="number"
                              :rules="nameRules"
                            >
                            </v-text-field>
                          </v-col>
                          <v-col cols="12">
                            <v-text-field
                              v-model="editedItem.carbohydrates"
                              label="Содержание углеводов ( в гр. )"
                              min="0"
                              outlined
                              hide-details
                              dense
                              type="number"
                              :rules="nameRules"
                            >
                            </v-text-field>
                          </v-col>
                          <v-col cols="12">
                            <v-text-field
                              v-model="editedItem.fat"
                              label="Содержание жиров ( в гр. )"
                              min="0"
                              outlined
                              hide-details
                              dense
                              type="number"
                              :rules="nameRules"
                            >
                            </v-text-field>
                          </v-col>
                          <v-col cols="12">
                            <v-text-field
                              v-model="editedItem.proteins"
                              label="Содержание белков ( в гр. )"
                              min="0"
                              outlined
                              hide-details
                              dense
                              type="number"
                              :rules="nameRules"
                            >
                            </v-text-field>
                          </v-col>
                          <v-col cols="12">
                            <v-text-field
                              v-model="editedItem.calories"
                              label="Содержание калориев ( в гр. )"
                              min="0"
                              outlined
                              hide-details
                              dense
                              type="number"
                              :rules="nameRules"
                            >
                            </v-text-field>
                          </v-col>

                          <v-col cols="12">
                            <div class="d-flex align-center">
                              <v-avatar>
                                <img :src="urlImage" />
                              </v-avatar>
                              <v-file-input
                                :label="!editedId ? 'Добавить фото' : 'Поменить фото'"
                                outlined
                                accept="image/png, image/gif, image/jpeg, image/jpg"
                                dense
                                v-model="file"
                                hide-details
                              ></v-file-input>
                              <!-- :rules="[v => !!v || 'Укажите изображение']" -->
                            </div>
                          </v-col>
                        </v-row>
                      </v-container>
                    </v-card-text>

                    <v-card-actions>
                      <v-spacer></v-spacer>
                      <v-btn color="blue darken-1" text @click="close">
                        Отмена
                      </v-btn>
                      <v-btn
                        color="blue darken-1"
                        :disabled="!valid"
                        text
                        @click="validate"
                      >
                        Сохранить
                      </v-btn>
                    </v-card-actions>
                  </v-form>
                </v-card>
              </v-dialog>
              <v-dialog v-model="dialogDelete" max-width="400px">
                <v-card outlined tile>
                  <v-card-title class="text-h5">
                    Вы уверены, что хотите удалить этот элемент?
                  </v-card-title>
                  <v-card-text
                    >Внимание. После того, как информация будет удалена, она не
                    может быть восстановлена!</v-card-text
                  >
                  <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="blue darken-1" text @click="closeDelete"
                      >Cancel
                    </v-btn>
                    <v-btn color="blue darken-1" text @click="deleteItemConfirm"
                      >OK
                    </v-btn>
                  </v-card-actions>
                </v-card>
              </v-dialog>
            </v-col>
          </v-row>
        </div>
      </template>
      <template v-slot:item.image="{ item }">
        <v-avatar rounded>
          <img :src="`${url}/${item.image}`" />
        </v-avatar>
      </template>
      <template v-slot:item.date="{ item }">
        <span>{{ dateformat(item.updatedAt) }}</span>
      </template>
      <template v-slot:item.public="{ item }">
        <v-chip :color="item.public ? 'green' : 'red'" dark small>
          {{ item.public ? "public" : "private" }}
        </v-chip>
      </template>
      <template v-slot:item.actions="{ item }">
        <v-menu offset-y>
          <template v-slot:activator="{ on, attrs }">
            <v-btn v-bind="attrs" v-on="on" icon>
              <v-icon>mdi-dots-horizontal</v-icon>
            </v-btn>
          </template>
          <v-list dense>
            <v-list-item link @click="editItem(item)">
              <v-list-item-title>Edit</v-list-item-title>
            </v-list-item>
            <v-list-item link @click="deleteItem(item)">
              <v-list-item-title>Delete</v-list-item-title>
            </v-list-item>
          </v-list>
        </v-menu>
      </template>
    </v-data-table>
    <v-snackbar v-model="snackbar" :timeout="2000">
      {{ snacktext }}
      <template v-slot:action="{ attrs }">
        <v-btn
          :color="snackstatus ? 'green' : 'red'"
          text
          v-bind="attrs"
          @click="snackbar = false"
        >
          Закрывать
        </v-btn>
      </template>
    </v-snackbar>
  </v-container>
</template>

<script>
import { mapGetters } from 'vuex';

export default {
  name: "Products",
  data: () => ({
    tags: [],
    valid: false,
    dialog: false,
    dialogDelete: false,
    snackbar: false,
    snacktext: "",
    snackstatus: true,
    headers: [
      { text: "Картина", value: "image", sortable: false, align: "start" },
      { text: "Название", value: "title" },
      { text: "Порция (120 гр.)", value: "portion" },
      { text: "Категория", value: "category" },
      { text: "Готовка (в мин.)", value: "cookingtime", sortable: false },
      { text: "Углеводы (в гр.)", value: "carbohydrates" },
      { text: "Жиры (в гр.)", value: "fat" },
      { text: "Белок (в гр.)", value: "proteins" },
      { text: "Калории (в гр.)", value: "calories" },
      { text: "Действия", value: "actions", sortable: false },
    ],
    desserts: [],
    search: "",
    editedIndex: -1,
    editedId: null,
    file: null,
    editedItem: {
      title: "",
      portion: "",
      cookingtime: "",
      carbohydrates: "",
      fat: "",
      image: "",
      proteins: "",
      calories: "",
      category: "",
    },
    defaultItem: {
      title: "",
      portion: "",
      cookingtime: "",
      carbohydrates: "",
      fat: "",
      image: "",
      proteins: "",
      calories: "",
      category: "",
    },
    nameRules: [(v) => !!v || "Укажите поля"],
  }),

  computed: {
    ...mapGetters(['url']),
    formTitle() {
      return this.editedIndex === -1 ? "Добавить" : "Редактировать";
    },
    urlImage() {
      if (this.file) {
        return URL.createObjectURL(this.file);
      } else if (this.editedId && !this.file) {
        return `${this.url}/${this.editedItem.image}`;
      } else {
        return "/icons/nophoto.jpg";
      }
    },
  },

  watch: {
    dialog(val) {
      val || this.close();
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
  },

  created() {
    this.initialize();
    this.getCategory();
  },

  methods: {
    validate() {
      if (this.$refs.form.validate()) {
        this.save();
      }
    },
    openSnackbar(text, status) {
      this.snackbar = true;
      this.snacktext = text;
      this.snackstatus = status;
    },

    async initialize() {
      const { data } = await this.$http.get("/product/all");
      if (data.status) {
        this.desserts = data.result;
      }
    },

    getChildCateg(ct) {
      return this.tags.find(c => c.title == ct).child;
    },

    async getCategory() {
      const { data } = await this.$http.get("/category/find?theme=product");
      if (data.status) {
        this.tags = data.result;
      }
    },

    editItem(item) {
      this.editedId = item._id;
      this.editedIndex = this.desserts.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },

    deleteItem(item) {
      this.editedId = item._id;
      this.editedIndex = this.desserts.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialogDelete = true;
    },

    async deleteItemConfirm() {
      const { data } = await this.$http.delete(`/product/delete/${this.editedId}`);
      if (data.status) {
        this.desserts.splice(this.editedIndex, 1);
      }
      this.openSnackbar(data.message, data.status);
      this.closeDelete();
    },

    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
        this.editedId = null;
        this.file = null;
      });
    },

    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
        this.editedId = null;
        this.file = null;
      });
    },

    async save() {
      const formdata = new FormData();
      Object.keys(this.editedItem).map((key) => {
        formdata.append(key, this.editedItem[key]);
      });
      if (this.file) {
        formdata.set("filename", Date.now() + "_" + this.file.name);
        formdata.append("file", this.file);
      }
      if (this.editedIndex > -1) {
        const { data } = await this.$http.put(`/product/edit/${this.editedId}`, formdata);
        if (data.status) {
          Object.assign(this.desserts[this.editedIndex], data.result);
        }
        this.openSnackbar(data.message, data.status);
      } else {
        const { data } = await this.$http.post("/product/add/", formdata);
        if (data.status) {
          this.desserts.push(data.result);
        }
        this.openSnackbar(data.message, data.status);
      }
      this.close();
    },
  },
};
</script>

<style>
</style>