<template>
  <v-navigation-drawer
    app
    color="grey darken-4"
    flat
    :mini-variant="!drawer"
    permanent
  >
    <v-list nav dense dark>
      <v-list-item-group>
        <v-list-item v-for="(link, i) in links" :key="i" link :to="link.url">
          <v-list-item-icon v-if="drawer">
            <v-icon v-text="link.icon"></v-icon>
          </v-list-item-icon>
          <v-tooltip right v-else color="grey darken-4">
            <template v-slot:activator="{ on, attrs }">
              <v-list-item-icon v-bind="attrs" v-on="on">
                <v-icon v-text="link.icon"></v-icon>
              </v-list-item-icon>
            </template>
            <span>{{ link.title }}</span>
          </v-tooltip>
          <v-list-item-title>{{ link.title }}</v-list-item-title>
        </v-list-item>
      </v-list-item-group>
    </v-list>
    <template v-slot:append>
      <v-list nav dense dark>
        <v-list-item link @click="logout">
          <v-list-item-icon v-if="drawer">
            <v-icon>mdi-logout-variant</v-icon>
          </v-list-item-icon>
          <v-tooltip right v-else color="grey darken-4">
            <template v-slot:activator="{ on, attrs }">
              <v-list-item-icon v-bind="attrs" v-on="on">
                <v-icon>mdi-logout-variant</v-icon>
              </v-list-item-icon>
            </template>
            <span>Logout</span>
          </v-tooltip>
          <v-list-item-title>Logout</v-list-item-title>
        </v-list-item>
      </v-list>
    </template>
  </v-navigation-drawer>
</template>

<script>
import { mapGetters, mapMutations } from "vuex";

export default {
  name: "Navigation",
  data: () => ({
    links: [
      { title: "Дашбоард", icon: "mdi-home", url: "/" },
      { title: "Пользователи", icon: "mdi-account", url: "/users" },
      { title: "Тренеры", icon: "mdi-account-school", url: "/trainers" },
      { title: "Админы", icon: "mdi-account-tie-hat", url: "/admins" },
      { title: "Продукт", icon: "mdi-baguette", url: "/products" },
      { title: "Схема питания", icon: "mdi-hamburger", url: "/mealplans" },
      { title: "Упражнение", icon: "mdi-dumbbell", url: "/exercises" },
      { title: "Тренировки", icon: "mdi-weight-lifter", url: "/workout" },
      { title: 'Категории', icon: 'mdi-chart-arc', url: '/categories' },
      { title: "Финансы", icon: "mdi-currency-usd", url: "/finance" },
      { title: "Feedback", icon: "mdi-message-processing", url: "/feedback" },
    ],
  }),
  computed: mapGetters(["drawer"]),
  methods: {
    ...mapMutations(['logout'])
  },
};
</script>